.home_container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4em;
}

.home_container h1 {
    font-size: 2.5em;
    margin-bottom: .5em;
}

.home_container h1 span {
    color: #4880FF;
}

.home_container p {
    margin-bottom: 1.5em;
    color: #5c5c5c;
}

.home_container img{
    width: 900px; 
    margin: 2em 0;
}